const  CHANGE_NAME = 'CHANGE_NAME';
const  CHANGE_PASS = 'CHANGE_PASS';
const  LOGIN = 'LOGIN';
const  AUTH_LOGIN = 'AUTH_LOGIN';


let initialState = {
    changedName: '',
    changedPass: null,
    status: null,
    name: null,
    pass: null,
    isAuth: false
}

let loginReducer = (state = initialState, action) => {
    switch(action.type){
        case CHANGE_NAME:{
            return{
                ...state,
                changedName: action.changeName
            }
        }
        case CHANGE_PASS:{
            return{
                ...state,
                changedPass: action.changePass
            }
        }
        case LOGIN:{
            let status = false;
            let formData = new FormData();
            formData.append('name', state.changedName);
            formData.append('pass', state.changedPass);
            fetch(`http://itsmeapi/login`, {method: 'POST', body: formData, headers: {'Content-Type': 'application/json'}}).then(function(response) {
                return response.json()
              }).then(function(body) {
                console.log(body);
              }); 
            return{
                ...state,
                status: status,
                changedName: '',
                changedPass: null
            } 
        }
        case AUTH_LOGIN:{
            return{
                ...state,
                name: action.data.name,
                pass: action.data.pass,
                isAuth: true
            }
        }
        default:{
            return state;
        }
    }

}

export const changeNameAC = (changeName) => ({type: CHANGE_NAME, changeName});
export const changePassAC = (changePass) => ({type: CHANGE_PASS, changePass});
export const logIn = () => ({type: LOGIN});
export const authLoginAC = (data) => ({type: AUTH_LOGIN, data});


export default loginReducer;